.menu{

	&__list{
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__item{
		margin-bottom: 25px;
		
		&:last-child{
			margin-bottom:0;
		}
	}

	&__link{
		color: #fff;
		font-weight: 400;
		font-size: 40px;
		text-transform: uppercase;

		&:hover{
			color:blue;
		}
	}
}