.fullscreen{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	background-color: black;
	z-index: 5;
	transition: .5s;

	opacity: 0;
	visibility: hidden;

	&__menu{
		display: flex;
		justify-content: center;
		align-items: center;

		height: 100%;
		width: 100%;

		.menu{
			&__item{
				transition-delay: 1s;
			}
		}
	}

}

.is-active{
	opacity: 1;
	visibility: visible;
}