.burger{
	width: 25px;
	height: 20px;
	display: block;
	cursor: pointer;

	position: fixed;
	top: 45px;
	right: 20px;
	z-index: 7;

	&__line{
		display: inline-block;
		height: 4px;
		width: 100%;
		background-color: #fff;
		border-radius: 5px;

		position: absolute;
		left: 0;

		&:nth-child(1){
			top: 0;
		}

		&:nth-child(2){
			top: 50%;
			transform: translateY(-50%);
		}

		&:nth-child(3){
			bottom: 0;
		}
	}
}

.lines{
	.burger__line{
		transform-origin: 50% 50%;

		&:nth-child(1){
			top: 47%;
			transform: rotate(45deg) translateY(-47%);
		}

		&:nth-child(2){
			display: none;
		}

		&:nth-child(3){
			transform-origin: 50% 50%;
			transform: rotate(-45deg) translateY(50%);
			bottom: 50%;
		}
	}
}