.slider{
	flex: 1;
	width: 100%;
	padding: 50px 0;
	position: relative;

	&__wrapper{
		overflow: hidden;
	}

	&__list{
		display: flex;
		transition: 1s;
		text-align: center;
		transition: .5s;
	}

	&__item{
		width: 100%;
	}

	&__pages{
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translateY(-50%);

		display: flex;
		justify-content: space-between;

		&-prev, &-next{
			display:inline-block;
			width: 15px;
			height: 22px;
			background:url(../img/arrow-link.png) center center no-repeat;
			position: absolute;
		}

		&-prev{
			left:-20px;
		}

		&-next{
			transform: rotate(180deg);
			right: -20px;
		}
	}
}