@charset "UTF-8";

.about {
  padding: 50px 0;
  background-color: #fff;
}

.about__list {
  display: flex;
}

.about__item {
  flex: 1;
  margin-right: 40px;
  position: relative;
}

.about__item:last-child {
  margin-right: 0;
}

.burger {
  width: 25px;
  height: 20px;
  display: block;
  cursor: pointer;
  position: fixed;
  top: 45px;
  right: 20px;
  z-index: 7;
}

.burger__line {
  display: inline-block;
  height: 4px;
  width: 100%;
  background-color: #fff;
  border-radius: 5px;
  position: absolute;
  left: 0;
}

.burger__line:nth-child(1) {
  top: 0;
}

.burger__line:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.burger__line:nth-child(3) {
  bottom: 0;
}

.lines .burger__line {
  transform-origin: 50% 50%;
}

.lines .burger__line:nth-child(1) {
  top: 47%;
  transform: rotate(45deg) translateY(-47%);
}

.lines .burger__line:nth-child(2) {
  display: none;
}

.lines .burger__line:nth-child(3) {
  transform-origin: 50% 50%;
  transform: rotate(-45deg) translateY(50%);
  bottom: 50%;
}

.content__title {
  margin-bottom: 35px;
}

.content__description {
  color: #d5d5d7;
  font-size: 19.97px;
  /* Приближение из-за подстановки шрифтов */
  font-style: italic;
}

.content__info {
  font-family: Heuristica;
  color: #b3b3b3;
  font-size: 21.3px;
}

.content__info-title {
  font-family: 'Lato';
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 1px;
  margin-bottom: 10px;
  color: #fff;
}

.content__info-subtitle {
  font-style: italic;
  margin-bottom: 20px;
}

.content__info-descr {
  margin-bottom: 50px;
  font-weight: 400;
}

.content__text {
  font-family: 'Source Sans Pro';
  color: #707070;
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 0.55px;
  line-height: 24px;
}

.content__letter {
  font-family: 'Source Sans Pro';
  color: #707070;
  font-size: 150px;
  font-weight: 900;
  opacity: .7;
  position: absolute;
  top: -10%;
  left: 0;
}

.country {
  padding: 100px 0 70px 0;
  background: url(../img/hazy-trees.png) 50% 65% no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  background-color: rgba(13, 3, 20, 0.7);
}

.country__content {
  text-align: center;
  padding: 0 10%;
}

.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: black;
  z-index: 5;
  transition: .5s;
  opacity: 0;
  visibility: hidden;
}

.fullscreen__menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.fullscreen__menu .menu__item {
  transition-delay: 1s;
}

.is-active {
  opacity: 1;
  visibility: visible;
}

.header {
  padding-top: 35px;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__burger {
  position: relative;
}

.intro {
  background: url(../img/intro-bg.png) 50% 20% no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  background-color: rgba(13, 3, 20, 0.9);
  display: flex;
  flex-direction: column;
}

.intro__main {
  display: flex;
  align-items: center;
}

.logo__link {
  display: inline-block;
}

.logo__img {
  max-width: 100%;
  max-height: 100%;
}

.main {
  flex: 1;
  margin: 70px 0;
}

.main .container {
  display: flex;
  align-items: center;
}

.menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu__item {
  margin-bottom: 25px;
}

.menu__item:last-child {
  margin-bottom: 0;
}

.menu__link {
  color: #fff;
  font-weight: 400;
  font-size: 40px;
  text-transform: uppercase;
}

.menu__link:hover {
  color: blue;
}

.slider {
  flex: 1;
  width: 100%;
  padding: 50px 0;
  position: relative;
}

.slider__wrapper {
  overflow: hidden;
}

.slider__list {
  display: flex;
  transition: 1s;
  text-align: center;
  transition: .5s;
}

.slider__item {
  width: 100%;
}

.slider__pages {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
}

.slider__pages-prev,
.slider__pages-next {
  display: inline-block;
  width: 15px;
  height: 22px;
  background: url(../img/arrow-link.png) center center no-repeat;
  position: absolute;
}

.slider__pages-prev {
  left: -20px;
}

.slider__pages-next {
  transform: rotate(180deg);
  right: -20px;
}

.travel__row {
  width: 100%;
  display: flex;
}

.travel__block {
  flex: 1;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: rgba(13, 3, 20, 0.9);
  text-align: center;
  padding: 145px 0;
}

.travel__block--train {
  background-image: url(../img/train-bg.png);
}

.travel__block--stockholm {
  background-image: url(../img/stockholm-bg.png);
}

.travel__block--people {
  background-image: url(../img/people-bg.png);
}

.travel__block--international {
  background-image: url(../img/international.png);
}

.travel__block--travelwith {
  background-image: url(../img/waves.png);
}

.travel__content {
  padding: 0 5%;
}

.travel__content .content__title {
  margin-bottom: 0;
}

.travel__content .content__info-descr {
  margin-bottom: 0;
}

.travel__button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.travel__button-facebook {
  margin-right: 25px;
}

/*base styles*/

* {
  box-sizing: border-box;
  transition: .5s;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  scroll-behavior: smooth;
}

.section {
  height: 100%;
  width: 100%;
}

.container {
  width: 1100px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.title {
  font-size: 60px;
  /* Приближение из-за подстановки шрифтов */
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
}

.title--fz37 {
  font-size: 37px;
  letter-spacing: 2px;
}

.button {
  display: inline-block;
  font-family: Lato;
  color: #cccccc;
  font-size: 16.38px;
  /* Приближение из-за подстановки шрифтов */
  font-weight: 700;
  cursor: pointer;
  background-color: transparent;
  text-transform: uppercase;
  padding: 8px 18px;
  border: 2px solid #fff;
  border-radius: 15px;
}

.button:hover {
  background-color: #cccccc;
  color: #fff;
  border-color: #cccccc;
}

.button--big {
  padding: 15px 0;
  min-width: 250px;
  color: #fff;
  border-color: transparent;
}

.button--blue {
  background-color: #3671b9;
}

.button--lightblue {
  background-color: #55c1e7;
}

.overflow {
  overflow: hidden;
}

/*base end*/

@media screen and (max-width: 992px) {
  .container {
    padding: 0 5%;
  }

  .title--fz37 {
    font-size: 25px;
  }
}

@media screen and (max-width: 768px) {
  .about__list {
    flex-wrap: wrap;
  }

  .about__item {
    margin-right: 0;
    width: 100%;
    flex-basis: 100%;
    margin-bottom: 20px;
  }

  .about__item:last-child {
    margin-bottom: 0;
  }

  .content__info-descr {
    font-size: 18px;
  }

  .travel__row {
    flex-direction: column;
  }
}

@media screen and (max-width: 480px) {
  .button--big {
    min-width: 130px;
  }
}