/*base styles*/
    *{
        box-sizing: border-box;
        transition: .5s;
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        font-family: 'Open Sans', sans-serif;
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }

    a{
        text-decoration: none;
    }

    .wrapper{
        position: relative;
        min-height: 100%;
        width: 100%;
        overflow: hidden;
        scroll-behavior: smooth;
    }

    .section{
        height: 100%;
        width: 100%;
    }

    .container{
        width: 1100px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;

        @include smallmonitor{
            padding: 0 5%;
        }
    }

    .title{
        font-size: 60px;/* Приближение из-за подстановки шрифтов */
        font-weight: 700;
        color: #fff;
        text-transform: uppercase;

        &--fz37{
            font-size: 37px;
            letter-spacing: 2px;

            @include smallmonitor{
                font-size: 25px;
            }
        }
    }

    .button{
        display: inline-block;
        font-family: Lato;
        color:  #cccccc;
        font-size: 16.38px;/* Приближение из-за подстановки шрифтов */
        font-weight: 700;
        cursor: pointer;
        background-color: transparent;
        text-transform: uppercase;

        padding: 8px 18px;
        border: 2px solid #fff;
        border-radius: 15px;

        &:hover{
            background-color:#cccccc;
            color: #fff;
            border-color: #cccccc;
        }

        &--big{
            padding: 15px 0;
            min-width: 250px;
            color: #fff;
            border-color: transparent;

            @include phones{
                min-width: 130px;
            }
        }

        &--blue{
            background-color: #3671b9;
        }

        &--lightblue{
            background-color: #55c1e7;
        }

    }

    .overflow{
        overflow:hidden;
    }

/*base end*/
