.content{

	&__title{
		margin-bottom: 35px;
	}

	&__description{
		color:  #d5d5d7;
		font-size: 19.97px;/* Приближение из-за подстановки шрифтов */
		font-style: italic;
	}

	&__info{
		font-family: Heuristica;
		color:  #b3b3b3;;
		font-size: 21.3px;

		&-title{
			font-family: 'Lato';
			font-size: 24px;
			font-weight: 700;
			letter-spacing: 1px;
			margin-bottom: 10px;
			color: #fff;
		}

		&-subtitle{
			font-style: italic;
			margin-bottom: 20px;
		}

		&-descr{
			margin-bottom: 50px;
			font-weight: 400;

			@include tablets{
				font-size: 18px;
			}
		}
	}

	&__text{
		font-family: 'Source Sans Pro';
		color:  #707070;
		font-size: 22px;
		font-weight: 300;
		letter-spacing: 0.55px;
		line-height: 24px;
	}

	&__letter{
		font-family: 'Source Sans Pro';
		color:  #707070;
		font-size: 150px;
		font-weight: 900;
		opacity: .7;

		position: absolute;
		top: -10%;
		left: 0;
	}


}