.about{
	padding: 50px 0;
	background-color: #fff;

	&__list{
		display: flex;

		@include tablets{
			flex-wrap: wrap;
		}
	}

	&__item{
		flex: 1;
		margin-right: 40px;
		position: relative;

		&:last-child{
			margin-right:0;
		}

		@include tablets{
			margin-right: 0;
			width: 100%;
			flex-basis: 100%;

			margin-bottom: 20px;

			&:last-child{
				margin-bottom:0;
			}
		}
	}
}