.travel{

	&__row{
		width: 100%;
		display: flex;

		@include tablets{
			flex-direction: column;
		}
	}

	&__block{
		flex: 1;
		-webkit-background-size: cover;
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		background-color: rgba(#0d0314, .9);

		text-align: center;
		padding: 145px 0;

		&--train{
			background-image: url(../img/train-bg.png);
		}

		&--stockholm{
			background-image: url(../img/stockholm-bg.png);
		}

		&--people{
			background-image: url(../img/people-bg.png);
		}

		&--international{
			background-image: url(../img/international.png);
		}

		&--travelwith{
			background-image: url(../img/waves.png);
		}
	}

	&__content{
		padding: 0 5%;

		.content{

			&__title{
				margin-bottom: 0;
			}

			&__info{
				&-descr{
					margin-bottom: 0;
				}
			}
		}
	}

	&__button{
		display: flex;
		justify-content: center;
		align-items: center;

		margin-top: 20px;

		&-facebook{
			margin-right: 25px;
		}
	}
}